import React, { useState } from 'react';
import { Button, Modal, Image } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router';


function RevokeModal() {
    const [show, setShow] = useState(true);
    const navigation = useNavigate()

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const dispatch = useDispatch();

    return (
        <Modal
            show={show}
            onHide={handleClose}
            backdrop="static"
            size='lg'
            keyboard={false}
            centered
        >
            <Modal.Body>
                <Button variant='close' className="position-absolute end-0 top-0 m-5" onClick={handleClose}></Button>
                <div className="py-lg-8 py-5 text-center">
                    <Image className='mb-6' src={baseUrl('media/images/error/warning.svg')} width="65" height="65" fluid/>
                    <h2 className='h3'>You're Running Extra Gallery/Feeds!</h2>
                    <p className="mb-6 fs-8 text-muted fw-medium">To enjoy uninterrupted services,kindly revoke the additional gallery/feed or upgrade your plan.</p>
                    <div className="text-center mb-4">
                        <Button variant='primary' className='m-2 btn-min-w' onClick={() => { navigation(`/price`); handleClose() }}>Upgrade Now</Button>
                        <Button variant='outline-primary' className='m-2 btn-min-w' onClick={handleClose}>Revoke</Button>
                    </div>
                    <div className='f-center'>
                        <Button variant='link' onClick={() => { navigation(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') :''}`); handleClose() }}>Skip</Button>
                    </div>
                </div>
            </Modal.Body>
        </Modal>
    );
}
export default RevokeModal;